@media (min-width: 500px) {
  .btn {
    left: 150px !important;
    right: 150px !important;
  }
}

@media (min-width: 1830px) {
  .btn {
    left: 700px !important;
    right: 700px !important;
  }
}

.btn {
  position: absolute;
  z-index: 10000 !important;
  background-color: rgb(92, 183, 218);
  transition: all 0.2s;
  color: #fff !important;
  padding: 15px 30px !important;
  text-decoration: none;
  font-size: 25px;
  border-radius: 18px;
  cursor: pointer;
  border: 0;
  bottom: 10px;
  left: 25px;
  right: 25px;
}

.btn:hover {
  background-color: rgb(67, 167, 206);
}
