@media (min-width: 500px) {
  .btnBack {
    font-size: 50px !important;
  }
}

.btnBack {
  position: absolute !important;
  z-index: 1000000000;
  top: 50px;
  left: 50px;
  font-size: 25px;
  text-decoration: none;
  color: #c2c2c2;
  transition: all 0.2s;
}

.btnBack:hover {
  color: #fff;
}
